import React from 'react';
import { useRef, useState, useEffect, useCallback } from 'react';
import FullCalendar from '@fullcalendar/react';
import dayGridPlugin from '@fullcalendar/daygrid';
import timeGridPlugin from '@fullcalendar/timegrid';
import interactionPlugin from '@fullcalendar/interaction';
import CalendarService from '../service/CalendarService';

import Modal from './Modal'

function Calender() {

    const [events, setEvents] = useState([]);
    // open  Modal 
    const [modalOpen, setModalOpen] = useState(false);

    const openModal = () => {
        setModalOpen(true); // modal open
    };
    const closeModal = () => {
        setModalOpen(false); // modal close
    };

    // Modal add    
    const addModal = (content, startDate, endDate, color) => {
        CalendarService.addEvent(content, startDate, endDate, color); // events   
        setModalOpen(false); // Modal 
    };

    useEffect(()=> {
        CalendarService.getCalendar().then((res) => {
            setEvents(res.data)

        })
    }, [events]);

  return (
    <div className="Calendar">

      <Modal open={modalOpen} close={closeModal} propFunction={addModal} header=" .">
      </Modal>

      <FullCalendar
            plugins={[dayGridPlugin, timeGridPlugin, interactionPlugin]}
            initialView="dayGridMonth" //  :  
            headerToolbar={{ // , ,       
                center: 'dayGridMonth,timeGridWeek,timeGridDay new',
            }}

            displayEventTime={false}

        customButtons = {{
            new : {
                text : "new",

                click : function() {
                    openModal();
                }

            }
        }}
        events={events} // event 
        eventColor="gray"
        nowIndicator

        dateClick={(e) => console.log(e.dateStr)}
        // eventClick={(e) => console.log(e.event.id)}

        //    DetailModal open

        /*
        eventClick={(e) => {
            const val = prompt('  !     "delete" ');
            console.log(e.event.title); //   title

            if(val) {
                if('delete' === val) {
                    console.log(' ');
                }

                else { // title 
                    const temp = events.find(e => e.title === events.event.title); //   
                    temp.title = val; //     

                    console.log(val);
                    console.log('  ');
                    console.log(temp.title);
                }
            }
          }
        }
        */

      />
    </div>
  ); // return 
}

export default Calender